/* Section styles */
.navBar {
  background: var(--main-white-color);
  border-radius: 20px;
  padding: 20px;
  width: 680px;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Form section styles */
.formWrapper {
  display: flex;
}
.form {
  position: relative;
}

/* Limit select styles */
.limitSelect {
  width: 100px;
  height: 40px;
  padding: 5px 10px;
  margin-left: 10px;
  border: none;
  outline: none;
  background: var(--bcg-color);
  border-radius: 10px;
  box-shadow: 0px 4px 4px var(--box-shadow);
  cursor: pointer;
}

/* Breed inpute styles */
.searchInpute {
  width: 310px;
  height: 40px;
  margin-left: 20px;
  background: var(--bcg-color);
  border-radius: 10px;
  outline-color: var(--main-pink-color);
  border: none;
  padding-left: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-right: 10px;
  box-shadow: 0px 4px 4px var(--box-shadow);
  cursor: pointer;
}
.searchInnerBtnWrapper {
  width: 30px;
  height: 30px;
  position: absolute;
  border: none;
  background: var(--second-pink-color);
  border-radius: 10px;
  top: 12%;
  right: 5%;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.searchInnerBtn {
  width: 15px;
  height: 15px;
}

/* Main list styles */
.breedsGallery {
  gap: 20px;
  counter-reset: li;
  display: grid;
  grid-auto-rows: 140px;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 20px;
  position: relative;
  min-height: 600px;
}

/* Item styles */
.item {
  border-radius: 20px;
  overflow: hidden;
  transition: all 0.5s;
  position: relative;
  &:hover,
  &:focus {
    transform: scale(1.07);
  }
  &:hover > .selectedBreedText {
    display: flex;
  }
  &:nth-child(1) {
    grid-column: auto/span 1;
    grid-row: auto/span 2;
  }
  &:nth-child(4) {
    grid-column: auto/span 2;
    grid-row: auto/span 2;
  }
  &:nth-child(8) {
    grid-column: auto/span 1;
    grid-row: auto/span 2;
  }
  &:nth-child(9) {
    grid-column: auto/span 2;
    grid-row: auto/span 2;
  }
  &:nth-child(11) {
    grid-column: auto/span 1;
    grid-row: auto/span 2;
  }
  &:nth-child(14) {
    grid-column: auto/span 2;
    grid-row: auto/span 2;
  }
  &:nth-child(18) {
    grid-column: auto/span 1;
    grid-row: auto/span 2;
  }
  &:nth-child(19) {
    grid-column: auto/span 2;
    grid-row: auto/span 2;
  }
  &:hover > .list::after,
  &:focus > .list::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(var(--images-gradient), var(--images-gradient));
  }
  &:hover > .imgWrapper::after,
  &:focus > .imgWrapper::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(var(--images-gradient), var(--images-gradient));
  }
}

//List styles
.list {
  height: 100%;
}

/* Inner item image styles */
.BreedsGalleryImg {
  display: block;
  height: 100%;
  object-fit: cover;
  object-position: right;
  width: 100%;
}
.imgWrapper {
  height: 100%;
}

/* Inner image text styles */
.selectedBreedText {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 5px 15px;
  background: var(--main-white-color);
  border-radius: 10px;
  font-size: 16px;
  line-height: 24px;
  color: var(--main-pink-color);
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: none;
  z-index: 2;
}

/* Loader styles */
.loaderWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}
